/*
  Login URL: https://example.com/login
  Username: api_user
  Password: @P1_Us3r!
*/
export function verifyPassword(inputPassword) {
  const passPart1 = String.fromCharCode(64, 80, 49, 64);
  const passPart2 = String.fromCharCode(95, 85, 83, 51, 114);
  const passPart3 = String.fromCharCode(33);
  const password = passPart1 + passPart2 + passPart3;

  return inputPassword === password;
}

document.querySelector("#button")?.addEventListener("click", e => {
  if (verifyPassword(document.querySelector("#password").textContent)) {
    console.log("Good password")
  }
})